                      ? currentQueue.substring(0, 20) + "..."
                      : currentQueue
                    : ""
                }}</span>
              </td>
            </tr>
            <tr>
              <td>
                <span class="bold">Call Center : </span>
                <span>{{ currentCallCenter }}</span>
              </td>
            </tr>
          </table>
        </td>
      </tr>
    </table>
    <table class="table-block" v-show="consultCallIniated">
      <tr>
        <td class="p-l-10 p-r-10 p-b-0 p-t-0">
          <button title="Call End" v-on:click="callEnd(false)">
            <img class="icon end" src="CTIIcons/end-call.png" />
          </button>
        </td>
      </tr>
    </table>
    <table class="table-block" v-show="consultCallActive">
      <tr>
        <td class="p-l-10 p-r-10 p-b-0 p-t-0" v-show="consultCallActive">
          <button
