            v-show="!consultOnHold"
            v-on:click="putOnHold(false)"
          >
            <img class="icon" src="CTIIcons/pause.png" />
          </button>
          <button
            title="Call Resume"
            v-show="consultOnHold"
            v-on:click="playFromHold(false)"
          >
            <img class="icon resume" src="CTIIcons/resume-call.png" />
          </button>
          <button
            title="Call End"
            v-on:click="callEnd(false)"
            v-bind:disabled="consultOnHold"
          >
            <img class="icon end" src="CTIIcons/end-call.png" />
          </button>
