          <button v-on:click="openLogin">
            <img class="icon" src="CTIIcons/login-icon.png" />
          </button>
        </td>
        <div id="app2" v-if="showApp2" v-on:login2-details="onLoginDetails"> <div id="login-app">
    <form method="post" id="login">
      <div class="container" style="
    margin-top: -14px;
">
      <tr>

        <td>
        <label for="uname"><b>Username</b></label>
        <input type="text" v-model="username" placeholder="Enter Cisco Finesse Username" name="uname" required>
        </td>
        <td>
        <label for="psw"><b>Password</b></label>
        <input type="password" v-model="password" placeholder="Enter Cisco Finesse Password" name="psw" required>
        </td>
        <td>
        <label for="extension"><b>Extension</b></label>
        <input type="text" v-model="extension" placeholder="Enter extension" name="extension" required>
        </td>
        <td>
        <label v-if="loginFailed" style="color:red">{{errorMessage}}</label>
        <button  v-on:click="login2" v-bind:disabled="loading"><i v-if="loading" class="fa fa-spinner fa-spin"></i> Login</button>
        </td>
      </tr>
       </div>
    </form>
    <div class="loading" v-show="loading" id="loading">Loading&#8230;</div>
  </div></div>
      </tr>
    </table>
  </div>
  <div class="loading" v-show="loading" id="loading">Loading&#8230;</div>
</template>
<script src="./script.js"></script>
