    reasonCodes: function () {
      this.loading = true;
      var that = this;
      let headers = {
        headers: {
          'Content-Type': 'application/xml',
          'Authorization': 'Basic ' + this.oauth
        }
      };
      axios.get(this.url + "User/" + this.username + "/ReasonCodes?category=ALL", headers).then((response) => {
        var xml = response.data;
        var reasonCodes = parser.parse(xml);
        var reason_codes = reasonCodes.ReasonCodes.ReasonCode;
        this.logoutStatus = [];
        this.userStatus = [];
        for (var i = 0; i < reason_codes.length; i++) {
          if (reason_codes[i].forAll == false) {
            if (reason_codes[i].category == "LOGOUT") {
              this.logoutStatus.push({
                id: reason_codes[i].uri.split("/").pop(),
                label: reason_codes[i].label
              });
            }
            else if (reason_codes[i].category == "NOT_READY") {
              this.userStatus.push({
                id: reason_codes[i].uri.split("/").pop(),
                label: reason_codes[i].label
              });
            }
          }
        }
        console.log(this.logoutStatus);
        console.log(this.userStatus);
        this.loading = false;
      }).catch((err) => {
        console.log(err);
        this.loading = false;
      });
    },
    
